<template>
    <div class="noticeDetail">
        <indexHeader></indexHeader>
        <div class="account-wrap plr30 bgWhite mt30 radius4 ft12">
           <div class="pdt20 pdb20 tc radius4" style="background-color: #f6f6f6;">
                <img :src="detail.thumbnail" alt="" class="wt60 radius50">
                <p class="mt5">{{currency_name}}</p>
           </div>
           <div class="mt20">
                <p class="ft14 p10">{{currency_name}}{{$t('coin.jianji')}}</p>
                <p class="p10">{{detail.title}}</p>
                <div class="mt30">
                    <p class="bdbWhite p10 ft14">{{$t('coin.jc')}}</p>
                    <ul>
                        <li class="flex alcenter bdbWhite pdt10 p10">
                            <p class="flex1">
                                <span class="color98">{{$t('coin.cname')}}:</span>
                                <span>{{detail.china_name}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.ename')}}:</span>
                                <span>{{detail.enlish_name}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.ejian')}}:</span>
                                <span>{{detail.enlish_jian}}</span>
                            </p>
                        </li>
                        <li class="flex alcenter bdbWhite pdt10 p10">
                            <p class="flex1">
                                <span class="color98">{{$t('coin.developer')}}:</span>
                                <span>{{detail.create_name}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.qj')}}:</span>
                                <span>{{detail.qk_award}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.qtime')}}:</span>
                                <span>{{detail.qk_time}}</span>
                            </p>
                        </li>
                        <li class="flex alcenter bdbWhite pdt10 p10">
                            <p class="flex1">
                                <span class="color98">{{$t('coin.ftime')}}:</span>
                                <span>{{detail.fb_time}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.wtype')}}:</span>
                                <span>{{detail.wk_way}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.sprice')}}:</span>
                                <span>{{detail.sc_price}}</span>
                            </p>
                        </li>
                        <li class="flex alcenter bdbWhite pdt10 p10">
                            <p class="flex1">
                                <span class="color98">{{$t('coin.hx')}}:</span>
                                <span>{{detail.hx_arithmetic}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.hbvol')}}:</span>
                                <span>{{detail.hb_allnumber}}</span>
                            </p>
                            <p class="flex1">
                                <span class="color98">{{$t('coin.qwsl')}}:</span>
                                <span>{{detail.qw_power}}</span>
                            </p>
                        </li>
                    </ul>
                    <div class="mt30"> 
                        <p>{{$t('coin.maints')}}</p>
                        <p class="mt10" v-html="detail.content"></p>
                    </div>
                    <div class="mt30"> 
                        <p>{{$t('coin.qttj')}}</p>
                        <div class="mt10 flex alcenter">
                            <img src="../assets/images/arr_right.png" alt="" class="wt40 pointer" @click="before">
                            <ul class="flex1 flex">
                                <li class="plr10 tc w10 pointer" v-for="item in list" @click="change(item.name)">
                                    <img :src="item.logo" alt="" class="wt60 h60 radius50">
                                    <p class="mt5">{{item.name}}</p>
                                </li>
                            </ul>
                            <img src="../assets/images/arr_left.png" alt="" class="wt40 pointer" @click="next">
                        </div>
                    </div>
                </div>
           </div>
        </div>
    </div>
</template>
<script>
import indexHeader from '@/view/indexHeader'
export default {
    name:'coinDetail',
    components:{indexHeader},
    data (){
        return{
           currency_name:'',
           list:[],
           detail:{},
           page:1,
           more:true
        }
    },
    watch:{
      
    },
    beforeCreate () {
        document.querySelector('html').setAttribute('style', 'background-color:#f8f8ff;')
    },
    beforeDestroy () {
        document.querySelector('html').removeAttribute('style')
    },
    created(){ 
        this.currency_name = this.$route.query.name;
        console.log(this.$route.query.name)
        this. getDetail();
        this.getAll();
    },
    mounted(){
      
    },
    methods:{
        change(name){
            let that = this;
            that.currency_name = name;
            that.getDetail();
        },
        getDetail(){
            this.$http({
                url: '/api/' + 'news/currency_get?currency_name='+this.currency_name,
                method:'get',
                headers: {'Authorization':  localStorage.getItem('token')},
            }).then(res=>{
                res = res.data;
                if(res.type  === 'ok'){
                    this.detail = res.message;
                    // this.setProperty();
                }else{
                    // layer.msg(res.message);
                }
            }).catch(error=>{
                console.log(error)
            })
        },
        getAll(){
            let that = this;
            this.$http({
                url: '/api/' + 'news/all_currency?page='+that.page+'&limit=10',
                method:'get',
            }).then(res=>{
                res = res.data;
                if(res.type  === 'ok'){
                    console.log(res)
                    var data = res.message.list;
                    that.more = data.length==res.message.limit? true : false;
                    if(data.length>0){
                        that.list = data;
                    }
                }else{
                    // layer.msg(res.message);
                }
            }).catch(error=>{
                console.log(error)
            })
        },
        next(){
            let that = this;
            let page = that.page;
            that.page++;
            that.getAll();
            if(!that.more){
                that.page = page;
                return layer.msg('没有更多了');
            }
        },
        before(){
            let that = this;
            if(that.page==1){
                return layer.msg('已经是第一页了')
            }
            that.page--;
            that.getAll();
        },
        setProperty(){
            var tags=document.getElementsByTagName('p');
            HTMLCollection.prototype.forEach=function(callback){
                    [].slice.call(this).forEach(callback);
            };
            tags.forEach(function(e, i){
                    e.style.backgroundColor='#666 !important'
            });
       }
    }
}
</script>
<style lang="scss" scoped>
    .account-wrap{
        width: 1200px;
        margin-left: auto;
        margin-right: auto;
        padding-top: 30px;
        padding-bottom: 30px;
    }
    .wt40{
        width: 40px;
    }
    .wt60{
        width: 60px
    }
    .h40{
        height: 40px;
    }
    .h60{
        height: 60px;
    }
    .radius50{
        border-radius: 50%;
    }
    .color98{
        color: #989898;
    }
    .bdbWhite{
        border-bottom: 1px solid #ececec;
    }
</style>






