<template>
    <div id="transfer-legal" v-if="coins.length">
		<!-- 定期 -->
       <div class="titles tit_box" >
			<p class="tit_color" style="margin-right: 40px;color: #fff;">{{$t('asset.deposit_fixed')}}</p>
			<p class="tit_color">{{$t('asset.deposit_assets')}}：{{fixed_assets.total}}</p>
			<p class="tit_color">{{$t('asset.deposit_num')}}：{{fixed_assets.num}}</p>
			<p class="tit_color">{{$t('asset.deposit_income')}}：{{fixed_assets.income}}</p>
			<p class="tit_color fr right_text flex_right"><span @click="go_record('fixed')" class="address">{{$t('asset.record')}}</span></p>
	   </div>
       <ul class="coins">
           <li v-for="(coin,index) in coins" :key="index" :class="{active:index == coinIndex}" @click="coinIndex = index;number = '';getAssetsFixed(coin.id)">
               {{coin.name}}
           </li>
       </ul>
       <div class="number">
           <input type="number" :placeholder="$t('asset.dNum')" v-model="number">
           <span style="color:#61688a">{{coins[coinIndex].name}}</span>
           <!-- <span class="all" @click="all">{{$t('td.all')}}</span> -->
       </div>
       <div class="balance" style="margin:30px 0">{{$t('asset.deposit_rate')}}</div>
	   <div class="mt10">
	     <div class="flex alcenter">
	       <select name id class="w30 mr20" v-model="coin_rate" @change="setFixedRate">
	         <option
	           v-for="(item,index) in fixed_list"
	           :key="index"
			   :value="item.id"
	         >{{item.desc}} ({{item.percent}})</option>
	       </select>
	     </div>
	   </div>
	   <div class="balance" style="margin:30px 0"></div>
       <button type='button' class="transfer" @click="transfer('fixed')">{{$t('asset.deposit_trans')}}</button>
	   <div class="splice_line"></div>
	   <!-- 活期 -->
	   <div class="titles tit_box" >
			<p class="tit_color" style="margin-right: 40px;color: #fff;">{{$t('asset.deposit_current')}}</p>
			<p class="tit_color">{{$t('asset.deposit_assets')}}：{{currency_assets.total}}</p>
			<p class="tit_color">{{$t('asset.deposit_num')}}：{{currency_assets.num}}</p>
			<p class="tit_color">{{$t('asset.deposit_income')}}：{{currency_assets.income}}</p>
			<p class="tit_color fr right_text flex_right"><span @click="go_record('current')" class="address">{{$t('asset.record')}}</span></p>
	   </div>
	   <ul class="coins">
	       <li v-for="(coin,index) in coins" :key="index" :class="{active:index == coinIndex2}" @click="coinIndex2 = index;number2 = '';getAssetsCurrency(coin.id)">
	           {{coin.name}}
	       </li>
	   </ul>
	   <div class="number">
	       <input type="number" :placeholder="$t('asset.dNum')" v-model="number2">
	       <span style="color:#61688a">{{coins[coinIndex2].name}}</span>
	       <!-- <span class="all" @click="all">{{$t('td.all')}}</span> -->
	   </div>
	   <div class="balance" style="margin:30px 0">{{current_list.desc}}{{current_list.percent}}</div>
	   <button type='button' class="transfer" @click="transfer('current')">{{$t('asset.deposit_trans')}}</button>
    </div>
</template>

<script>
export default {
  data() {
    return {
      token: "",
      selectedCoin: { name: "", id: "" },
      lever_coins:[],
	  fixed_list:[],
	  current_list:{},
      coins: [],
	  addId: 0,
      coinIndex: 0,
      types:[this.$t('asset.lever_account'),this.$t('asset.change_account')],
      number:'',
	  coinIndex2: 0,
	  number2:'',
	  coin_rate:"",
	  fixed_assets:{
	  	total:0,
	  	num:'0.000',
	  	income:'0.000'
	  },
	  currency_assets:{
	  	total:0,
	  	num:0,
	  	income:0
	  }
    };
  },
  created() {
    this.getCoins();
	this.getRate();
  },
  methods: {
    getCoins() {
      this.token = window.localStorage.getItem("token") || "";
	  var that = this
      this.$http({
        url: "/api/fixed/get_currency_lists",
        method: "get",
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
			console.log(res)
			that.coins = res.data.message
			that.getAssetsFixed(that.coins[that.coinIndex].id)
			that.getAssetsCurrency(that.coins[that.coinIndex2].id)
        }
      });
    },
	getAssetsFixed(id) {
		 var that = this
	  this.token = window.localStorage.getItem("token") || "";
	  this.$http({
	    url: "/api/fixed/get_user_fixed_deposit_total",
	    method: "post",
		data:{
			currency:id
		},
	    headers: { Authorization: this.token }
	  }).then(res => {
	    if (res.data.type == "ok") {
			console.log(res)
			that.fixed_assets = res.data.message
	    }
	  });
	},
	getAssetsCurrency(id) {
		 var that = this
	  this.token = window.localStorage.getItem("token") || "";
	  this.$http({
	    url: "/api/fixed/get_user_current_deposit_total",
	    method: "post",
		data:{
			currency:id
		},
	    headers: { Authorization: this.token }
	  }).then(res => {
	    if (res.data.type == "ok") {
			console.log(res)
			that.currency_assets = res.data.message
	    }
	  });
	},
	getRate(){
		this.token = window.localStorage.getItem("token") || "";
		this.$http({
		  url: "/api/fixed/get_deposit_percent",
		  method: "get",
		  headers: { Authorization: this.token }
		}).then(res => {
		  if (res.data.type == "ok") {
		    console.log(res.data)
			console.log(res.data.message.current[0])
			this.coin_rate = res.data.message.fixed[0].id
			this.fixed_list = res.data.message.fixed
			this.current_list = res.data.message.current[0]
		  }
		});
	},
	setFixedRate(e) {
	  // if (this.addId != this.$t("asset.currency")) {
	  //   let index = e.target.selectedIndex;
	  //   let opt = e.target.querySelectorAll("option")[index];
	  //   this.nowCoin = opt.text;
	  //   this.getList();
	  // }
	 // var addr= this.coins.filter(item=>item.currency_name==this.addId)[0].contract_address;
	  // console.log(e);
	  // this.coin_rate = this.fixed_list[e.target.selectIndex].id
	  // this.getAddress(addr);
	  // this.address = ''
	},
	go_record(item){
		this.$router.push({
		    name:'depositRecord',
		    query:{
		      deposit_type:item
		    }
		})
	},
    all(){
        if(this.types[0] == this.$t('asset.lever_account')){
            this.number = this.lever_coins[this.coinIndex].lever_balance
        }else{
            this.number = this.coins[this.coinIndex].change_balance
        }
    },
    transfer(type){

		if(type=="fixed"){
			console.log(this.coins[this.coinIndex].id)
			console.log(this.number)
			console.log(this.coin_rate)
			if(this.number == ''){
			    layer.msg(this.$t('asset.pNum'));
				return;
			}
			let data = {};
			data.currency = this.coins[this.coinIndex].id;
			data.num = this.number;
			data.percent = this.coin_rate;
			data.deposit_type = 1;
			this.$http({
			 url:'/api/fixed/fixed_deposit_opreation',
			 method:'post',
			 data:data,
			 headers: { 'Authorization': this.token }
			}).then(res => {
			 if(res.data.type == 'ok'){
				 layer.msg(res.data.message)
				 // setTimeout(() => {
					//  this.$router.push({path:'/legalAccount',query:{currency_id:this.coins[this.coinIndex].currency}})
				 // },2000)
			 }else{
				 layer.msg(res.data.message)
			 }
			})
		}else{
			console.log(this.coins[this.coinIndex2].id)
			console.log(this.number2)
			console.log(this.current_list.id)
			if(this.number2 == ''){
			    layer.msg(this.$t('asset.pNum'));
				return;
			}
			let data = {};
			data.currency = this.coins[this.coinIndex2].id;
			data.num = this.number2;
			data.percent = this.current_list.id;
			data.deposit_type = 2;
			this.$http({
			 url:'/api/fixed/fixed_deposit_opreation',
			 method:'post',
			 data:data,
			 headers: { 'Authorization': this.token }
			}).then(res => {
			 if(res.data.type == 'ok'){
				 layer.msg(res.data.message)
				 // setTimeout(() => {
					//  this.$router.push({path:'/legalAccount',query:{currency_id:this.coins[this.coinIndex].currency}})
				 // },2000)
			 }else{
				 layer.msg(res.data.message)
			 }
			})
		}



		 // else {
   //          let data = {};
   //          data.number = this.number;
   //          data.from = this.types[0] == this.$t('asset.lever_account')?'lever':'change';
   //          data.to = this.types[0] == this.$t('asset.lever_account')?'change':'lever';
   //          data.wallet_id = this.coins[this.coinIndex].id;
   //          this.$http({
   //              url:'/api/wallet/transfer',
   //              method:'post',
   //              data:data,
   //              headers: { 'Authorization': this.token }
   //          }).then(res => {
   //              if(res.data.type == 'ok'){
   //                  layer.msg(res.data.message)
   //                  setTimeout(() => {
   //                      this.$router.push({path:'/legalAccount',query:{currency_id:this.coins[this.coinIndex].currency}})
   //                  },2000)
   //              }else{
   //                  layer.msg(res.data.message)
   //              }
   //          })
   //      }
    }
  }
};
</script>

<style lang='scss' scoped>
	.tit_color{
		margin-right: 40px;
		color: #fff;
		font-size:16px;
	}
	.tit_box{
		display: flex;
		align-items: center;
		padding-bottom: 30px;
	}
	.splice_line{
		width: 100%;
		background-color:#6b6b6b;
		height: 1px;
		margin: 40px 0;
	}
	.flex_right{
		flex: 1;
		text-align: right;
		color: #5697f4;
	}
#transfer-legal {
  padding: 0 30px;
  color: #c7cce6;
  > .titles {
      padding: 20px 0;
    font-size: 24px;
    color: #61688a;
  }
  > .coins {
    display: flex;
    // justify-content: center;
    flex-wrap: wrap;
    li {
      margin-right: 20px;
      padding: 5px 20px;
      color: #fff;
      background: #234165;
      margin-bottom: 10px;
    }
    .active {
      background: #7a98f7;
    }
  }
  >.types{
      margin: 30px 0;
    //   width: 400px;
      height: 80px;
      display: flex;
      justify-content: space-between;
      line-height: 40px;
      .types-r{
          display: flex;
      }
      img{
          margin: 25px;
          width: 30px;
          height: 30px;
      }
  }
  .number{
      margin: 30px 0 0;
    //   width: 430px;
      line-height: 40px;
      input{
        border:none;
        color:#f2f5ff;
        background: none;
        line-height: 40px;
        width: 755px;
        border-bottom: 2px solid #3a4975;

      }
      .all{
          margin-left: 10px;
          padding-left: 10px;
          border-left: 1px solid #ccc;
      }
  }
  .transfer{
      display: block;
      margin: 0 auto;
      line-height: 40px;
      width: 200px;
      background: #7a98f7;
      color: #fff;
      border: none;
  }
}
select {
  background-color: transparent;
  padding: 10px;
  color: #c7cce6;
  font-size: 18px;
  border-radius: 3px;
  border: 1px solid #4e5b85;
}
select.ft14 {
  font-size: 14px;
}
select > option {
  padding: 10px 0;
  background-color: transparent;
  color: #2d2d2d;
}
</style>



