<template>
  <div class>
    <div class="header fColor1">
      <p class="fl">
        {{$t('asset.all_assets')}}：
        <span class="asset_name">$</span>
        <span class="asset_num">{{totle | toFixeds}}</span>
      </p>
      <p class="fr right_text">
        <span class="address" @click="withdraw_address">绑定提币地址</span>
      </p>
    </div>
    <div class="content fColor1 ft12">
      <div class="content_top flex alcenter fColor2">
        <p class="flex1 tc">
          {{$t('asset.currency')}}
          <i></i>
        </p>
        <p class="flex1 tc">{{$t('asset.canuse')}}</p>
        <p class="flex1 tc">{{$t('asset.frezz')}}</p>
        <p class="flex1 tc">{{$t('asset.conversion')}}($)</p>
        <p class="flex1 tc">{{$t('td.do')}}</p>
      </div>
      <ul class="content_ul">
        <li
          v-for="(item,index) in asset_list"

          :key="index"
        >
          <div class="content_li flex alcenter between">
            <p class="flex1 tc">{{item.currency_name}}</p>
            <p class="flex1 tc">
              <!-- <span v-if="item.currency_name=='BTC'||item.currency_name=='ETH'">{{item.change_balance || '0.00' | toFixeds8}}</span> -->
              <span>{{item.change_balance || '0.00' | toFixeds8}}</span>
            </p>
            <p class="flex1 tc">
              <!-- <span v-if="item.currency_name=='BTC'||item.currency_name=='ETH'">{{item.lock_change_balance || '0.00' | toFixeds8}}</span> -->
              <span>{{item.lock_change_balance || '0.00' | toFixeds8}}</span>
            </p>
            <p class="flex1 tc">
              <!-- <span v-if="item.currency_name=='BTC'||item.currency_name=='ETH'">{{item.cny_price * item.change_balance || '0.00' | toFixeds8}}</span> -->
              <span>{{item.usdt_price * item.change_balance || '0.00' | toFixeds8}}</span>
            </p>
            <p class="flex1 tc operation">
              <span @click="excharge(index,item.currency,item.currency_type,item.contract_address)">{{$t('asset.charging')}}</span>
              <span @click="withdraw(index,item.currency,item.currency_name,item.contract_address)" style="padding: 0 5px;">{{$t('asset.withdraw')}}</span>
              <span @click="$router.push({path:'/transferLever',query:{id:item.currency}})">{{$t('asset.transfer')}}</span>
              <span @click="rec(index,item.currency)">{{$t('asset.record')}}</span>
            </p>
          </div>
          <!--充币区-->
          <div class="hide_div" v-if="index == active">
            <p class="fColor2 ft12">{{$t('asset.address_charge')}}</p>
            <p class="mt50 mb50">
              <span class="ft18 fColor1 excharge_address" :class="{'bg':flags}">{{excharge_address}}</span>
              <span id="copy" @click="copy" class="copy ft14">{{$t('asset.copy')}}地址</span>

              <span class="ewm_wrap">
                <span class="ewm ft14" @click="show_ewm">{{$t('asset.ercode')}}</span>
                <div class="ewm_img" id="code" :class="{'hide':isHide}"></div>
              </span>
              <span @click="copy2" class="pdlr20 blue ft14 copy2" v-if="ishowTag">复制地址标签</span>
            </p>
            <p class="ft12 fColor2 mb15">{{$t('asset.reminder')}}</p>
            <ul class="tips_ul ft12 fColor2" style="list-style:disc inside">
              <li class="tips_li" style="list-style:disc inside" v-for="item in tip_list">{{item}}</li>
            </ul>
          </div>
          <!--提币区-->
          <div class="hide_div" v-if="index == active01">
            <p class="fColor2 ft12 mb15">{{$t('asset.address_width')}}</p>
            <input class="address_inp fColor1 mb30" type="text" disabled v-model="address">
            <p class="fColor2 ft12 mb15 flex between alcenter">
              <span>{{$t('td.num')}}</span>
              <span>
                {{$t('asset.canuse')}}：
                <span class="use_num">{{balance || '0.00' | toFixeds}}</span>
                {{coinname}}
                <span>
                </span>
              </span>
            </p>
            <label class="num_lab flex between mb30">
              <input class="fColor1" type="text" :placeholder="min_number" v-model="number">
              <span>{{coinname}}</span>
            </label>
            <div class="flex mb30">
              <div class="left_inp_wrap flex1">
                <p class="fColor2 ft12 mb15">
                  <span>{{$t('asset.ratenum')}}</span>
                  <span>{{ratenum}}%</span>
                </p>
                <label class="range_lab flex alcenter between">
                  <span>{{number * ratenum /100 | toFixeds}}</span>
                  <span>{{coinname}}</span>
                </label>
              </div>
              <div class="right_inp_wrap flex1">
                <p class="mb15">
                  <span class="fColor2 ft12">{{$t('asset.havenum')}}</span>
                </p>
                <label class="get_lab flex alcenter between">
                  <span>{{number - number * ratenum /100 | toFixeds}}</span>
                  <span>{{coinname}}</span>
                </label>
              </div>
            </div>
           <!-- <div v-if="is_show_memo">
               <p class="fColor2 ft12 mb15">memo</p>
              <input class="address_inp fColor1 mb30" type="text" v-model="taginput" placeholder="请输入memo(非必填)">
           </div> -->
		   <!-- 验证码 -->
		   <div class="mb30">
		     <!-- <div class="left_inp_wrap flex1">
		       <p class="fColor2 ft12 mb15">{{$t('lever.psw')}}</p>
		       <input class="address_inp fColor1 mb30" type="password" v-model="password">
		     </div> -->
        <p class="fColor2 ft12 mb15">{{$t('set.code')}}</p>
        <div class="flex alcenter w100">
          <input class="address_inp fColor1 flex1" type="text" v-model="code">
          <button style="width: 100px;" class="btn curPer" @click="setTime">{{$t('set.sendCode')}}</button>
        </div>
		  </div>
			<p class="fColor2 ft12 mb15">{{$t('lever.psw')}}</p>
			<input class="address_inp fColor1 mb30" type="password" v-model="password">

            <div class="flex">
              <div class="" style="width：80%">
                <p class="ft12 fColor2 mb15">{{$t('asset.reminder')}}</p>
                <ul class="tips_ul ft12 fColor2" style="list-style:disc inside">
                  <li
                    class="tips_li"
                    style="list-style:disc inside"
                    v-for="item in tip_list01"
                  >{{item}}</li>
                </ul>
              </div>
              <div class="flex1 tc">
                <button class="withdraw_btn" @click="mention">{{$t('asset.withdraw')}}</button>
              </div>
            </div>
          </div>
          <!--记录区-->
          <div class="hide_div rec-box" v-if="index == active02">
            <div class="rec-con">
              <div class="rec-title">
                <span>{{$t('td.num')}}</span>
                <span>{{$t('asset.record')}}</span>
                <span>{{$t('td.time')}}</span>
              </div>
              <ul class="rec-list">
                <li v-for="(reItem,reIndex) in recData" :key="reIndex">
                  <span>{{reItem.value || '0.00' | toFixeds}}</span>
                  <span>{{reItem.info}}</span>
                  <span>{{reItem.created_time}}</span>
                </li>
              </ul>
              <p class="more" @click="moreData()">{{more}}</p>
            </div>
          </div>
        </li>
      </ul>
      <!-- <div class="tc ft16 fColor1 mt50" v-show="asset_list.length<=0">暂无数据</div> -->
    </div>
    <!-- 划转弹窗 -->
    <div class="transfer-modal flex" v-show="transferData.modalShow">
      <div class="transfer-mask">
        <div class="transfer-header">
          <h3>划转</h3>
          <p @click="closes()">X</p>
        </div>
        <div class="transfer-content">
          <div>
            <p>币种</p>
            <p>{{transferData.transferName}}</p>
            <p>可用余额</p>
            <p>{{transferData.transferBalance}}</p>
          </div>
          <div>
            <p>从</p>
            <select v-model="transferData.start">
              <option v-for="item in balanceList" :value="item">{{item}}</option>
            </select>
          </div>
          <div>
            <p>至</p>
            <select v-model="transferData.end">
              <option v-for="item in balanceList" :value="item">{{item}}</option>
            </select>
          </div>
          <div>
            <p>划转数量</p>
            <input type="text" placeholder="请输入划转数量">
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import indexHeader from "@/view/indexHeader";
import left from "@/view/left";
import "@/lib/clipboard.min.js";
import "@/lib/jquery.qrcode.min.js";
export default {
  name: "finance",
  filters: {
    toFixeds: function(value) {
      value = Number(value);
      return value.toFixed(8);
    },
    toFixeds8: function(value) {
      value = Number(value);
      return value.toFixed(8);
    }
  },
  data() {
    return {
      totle: "",
    notes: "",
    code:'',
	  disable:false,
	  codeText: "获取验证码",
      recData: [],
      token: "",
      flags: false,
      flag: false,
      isHide: true,
      active: "a",
      active01: "a",
      active02: "a",
      addr: "",
      url: "",
      excharge_address: "",
      address: "",
      number: "",
      rate: "",
      coinname: "",
      balance: "",
      ratenum: "",
      reachnum: "",
      min_number: "",
      currency: "",
      password:'',
      asset_list: [],
      tip_list: [
        this.$t('asset.tips01'),
        this.$t('asset.tips02')
      ],
      tip_list01: [
        this.$t('asset.tips01'),
        this.$t('asset.tips02')
      ],
      page: 1,
      more: this.$t('td.more'),
      balanceList: [this.$t('asset.fince_account'), this.$t('asset.lever_account')],
      transferData: {
        modalShow: false,
        transferName: "",
        transferBalance: "",
        start: "",
        end: ""
      },
      status:'',
      ishowTag:false,
      extension_code:'',//地址标签
      taginput:'',
      is_show_memo:0,
      account_number:''
    };
  },
  components: {
    indexHeader,
    left
  },
  methods: {
     userInfo(){
      this.$http({
        url: '/api/'+'user/info',
        method:'get',
        data:{},  
        headers: {'Authorization':  localStorage.getItem('token')},    
      }).then(res=>{
        if(res.data.type == 'ok'){
          console.log(res.data.message.account_number)
          this.account_number = res.data.message.account_number;
        }
        }).catch(error=>{
              
      })               
    },
    sendCode(url) {
      console.log(url)
      this.$http({
        url: "/api/" + url,
        method: "post",
        data: {
          user_string: this.account_number,
          scene:'tibi'
        }
      }).then(res => {
        layer.msg(res.data.message);
      });
    },
    setTime(e) {
      let that = this;
      if (e.target.disabled) {
        return;
      } else {
        var reg = /^1[3456789]\d{9}$/;
        var url = "sms_send";
        var emreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
        if (emreg.test(this.account_number)) {
          url = "sms_mail";
          this.isMb = false;
        }else{
          url = "sms_send"; 
          this.isMb = true;
        }
        this.sendCode(url);
        var time = 60;
        var timer = null;
        timer = setInterval(function() {
          e.target.innerHTML = time + 's';
          e.target.disabled = true;
          console.log(time)
          if (time == 0) {
            console.log(time)
            e.target.innerHTML = that.$t('set.sendCode');
            e.target.disabled = false; 
            clearInterval(timer);
            return;
          }
          time--;
        }, 1000);
      }
    },
	getCode() {
		// alert(localStorage.getItem("user_id"));
		var that = this;
		this.$http({
		  url: this.API+"/walletMiddle/SendVerificationcode",
		  method: "GET",
		  params:{
			user_id:localStorage.getItem("user_id")
		  }
		}).then(res => {
			console.log(res)
		  if (res.data.code == 0) {
			layer.msg("发送成功");
			var times = 30
			var timer = setInterval(() => {
			  times--;
			  that.codeText = times + "s";
			  that.disable = true;
			  if (times <= 0) {
				clearInterval(timer);
				that.codeText = '获取验证码';
				that.disable = false;
			  }
			}, 1000);
		  } else {
			layer.msg(res.data.msg);
		  }
		});
	},
    goRecord() {
      this.$router.push({ name: "coinRecord" });
    },
    init() {
      var that = this;
      var clipboard = new Clipboard(".copy");
      clipboard.on("success", function(e) {
        layer.alert(that.$t('set.copysuccess'));
      });
      clipboard.on("error", function(e) {
        alert(that.$t('set.copyfail'));
      });
    },
    //充币
    excharge(index, currency,currency_name,addr,is_daibi) {
      this.excharge_address = '';
      if(this.status == 1){
        this.currency = currency;
        if (this.flag) {
          this.flag = false;
          this.active = "a";
          this.active01 = "a";
          this.active02 = "a";
        } else {
          this.flag = true;
          this.active = index;
          this.active01 = "a";
          this.active02 = "a";
        }
        // var name=is_daibi==1?'EOSTOKEN':currency_name;
        this.sendData(currency_name,addr);
        this.getNum(currency,currency_name);
      }else{
        layer.alert(this.$t('asset.noopen'))
      }

    },

    sendData(currency,addr) {
      var that = this;
      this.$http({
        url: this.API + "/walletMiddle/GetRechargeAddress",
        get: "get",
        params: { coin_type: currency,user_id:localStorage.getItem('user_id') ,contract_address:addr},
        // headers: { Authorization: that.token }
      })
        .then(res => {
          if (res.data.code == 0) {
            that.excharge_address = res.data.data.address;
            // 生成二维码
            $("#code").qrcode({
              width: 100, //宽度
              height: 100, //高度
              text: res.data.data.address
            });
          } else {

          }
        })
        .catch(error => {
          console.log(error);
        });
    },
    //提币
    withdraw(index, currency,name,addr) {
      // this.is_show_memo=is_show_memo;
      if(this.status == 1){
        this.currency = currency;
        if (this.flag) {
          this.flag = false;
          this.active = "a";
          this.active01 = "a";
          this.active02 = "a";
        } else {
          this.flag = true;
          this.active01 = index;
          this.active = "a";
          this.active02 = "a";
        }
        this.getNum(currency,name);
        this.getAddress(name,addr);
      }else{
        layer.alert(this.$t('asset.noopen'));
      }

    },
    //记录
    rec(index, currency) {
      this.currency = currency;
      this.recData = [];
      if (this.flag) {
        this.flag = false;
        this.active = "a";
        this.active01 = "a";
        this.active02 = "a";
      } else {
        this.flag = true;
        this.active02 = index;
        this.active = "a";
        this.active01 = "a";
        this.$http({
          url: "/api/wallet/legal_log",
          method: "post",
          data: { type: 2, currency: currency, page: 1 },
          headers: { Authorization: this.token }
        }).then(res => {
          if (res.data.type == "ok") {
            this.recData = res.data.message.list;
          }
        });
      }
    },
    getNum(currency,currency_type) {
      var that = this;
      $.ajax({
        url: "/api/" + "wallet/get_info",
        method: "post",
        data: {
          currency: currency
        },
        dataType: "json",
        async: true,
        beforeSend: function beforeSend(request) {
          request.setRequestHeader("Authorization", that.token);
        },
        success: function(res) {
          if (res.type == "ok") {
            that.coinname = res.message.name;
            that.balance = res.message.change_balance;
            that.min_number =
              that.$t('asset.minnum') + parseFloat(res.message.min_number).toFixed(2);
            that.minnumber = res.message.min_number;
            that.ratenum = res.message.rate;
            that.reachnum = 0.0;
            that.rate = res.message.rate;
            if(res.message.name=='EOSTOKEN'){
              that.ishowTag=true;
            }
            that.extension_code = res.message.extension_code;
          } else {
          }
        }
      });
    },
    // 提币按钮
    mention() {
      var that = this;
      var currency = this.currency;
      var address = this.address;
      var number = this.number;
      var rate = this.rate;
      var min_number = this.minnumber;
      var password= this.password;
      var notes = this.notes;
      var code = this.code;
      // var taginput=this.taginput;
      if (!address) {
        layer.msg(this.$t('asset.enterAdderses'));
        return;
      }
      if (!number) {
        layer.msg(this.$t('asset.enterNum'));
        return;
      }
      if (number - 0 < min_number) {
        return layer.msg(this.$t('asset.lessMin'));
      }
      if (!password) {
        layer.msg(this.$t('lever.ppwd'));
        return;
      }
      if (!code) {
        layer.msg(this.$t('set.enterCode'));
        return;
      }
      // if(this.coinname=='BAS'){
      //   if(!taginput){
      //     return layer.msg('请输入标签')
      //   }
      // }
      $.ajax({
        type: "POST",
        url: "/api/" + "wallet/out",
        data: {
          currency: currency,
          number: number,
          rate: rate,
          address: address,
          password:password,
          verify:code
          // memo:taginput,
        },
        dataType: "json",
        async: true,
        beforeSend: function beforeSend(request) {
          request.setRequestHeader("Authorization", that.token);
        },
        success: function(res) {
          if (res.type == "ok") {
            layer.msg(res.message);
            setTimeout(() => {
              window.location.reload();
            }, 1500);
          } else if(res.type == "not"){
            layer.msg(res.message);
            setTimeout(() => {
              layer.msg(that.$t('set.yorn'), {
                time: 0 //不自动关闭
                ,btn: [that.$t('set.yes'),that.$t('set.no')]
                ,yes: function(index){
                  layer.closeAll();
                  that.$router.push("/bindmpwd");
                }
              });
            }, 1000);

          } else {
            layer.msg(res.message);
          }
        }
      });
    },
    exchange() {},
    //复制
    copy() {
      var that = this;
      var clipboard = new Clipboard(".copy", {
        text: function() {
          return that.excharge_address;
        }
      });
      clipboard.on("success", function(e) {
        that.flags = true;
        layer.msg(that.$t('set.copysuccess'));
      });
      clipboard.on("error", function(e) {
        that.flags = false;
        layer.msg(that.$t('set.recopy'));
      });
    },
    copy2(e){
       var that = this;
      var clipboard = new Clipboard(".copy2", {
        text: function() {
          return that.extension_code;
        }
      });
      clipboard.on("success", function(e) {
        that.flags = true;
        layer.msg(that.$t('set.copysuccess'));
      });
      clipboard.on("error", function(e) {
        that.flags = false;
        layer.msg(that.$t('set.recopy'));
      });
    },
    record() {
      this.$router.push({ name: "finanrec" });
    },
    withdraw_address() {
      this.$router.push({ name: "withdraw_address" });
    },
    show_ewm() {
      if (this.isHide) {
        this.isHide = false;
      } else {
        this.isHide = true;
      }
    },
    getdata() {
      var that = this;
      this.$http({
        url: "/api/" + "wallet/list",
        method: "post",
        data: {},
        headers: { Authorization: that.token }
      })
        .then(res => {
          if (res.data.type == "ok") {
            that.asset_list = res.data.message.change_wallet.balance;
            that.totle = res.data.message.change_wallet.totle;
            that.status = res.data.message.is_open_CTbi;
          } else {
            return;
          }
          // this.asset_list.forEach((item, index) => {
          //   this.$http({
          //     url: "/api/wallet/legal_log",
          //     method: "post",
          //     data: { type: "0", currency: item.currency, page: this.page },
          //     headers: { Authorization: this.token }
          //   }).then(res => {
          //     if (res.data.type == "ok") {
          //       this.recData[index] = res.data.message.list;
          //     }
          //   });
          // });
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 加载更多
    moreData() {
      let that = this;
      that.page = that.page + 1;
      this.$http({
        url: "/api/wallet/legal_log",
        method: "post",
        data: { type: 2, currency: that.currency, page: that.page },
        headers: { Authorization: this.token }
      }).then(res => {
        if (res.data.type == "ok") {
          var datas = that.asset_list;
          for (let i in datas) {
            if (that.currency == datas[i].currency) {
              that.active02 = i;
              if(res.data.message.list.length > 0){
                that.recData= that.recData.concat(res.data.message.list);
              }else{
                that.more=that.$t('cuy.nomore');
              }

            }
          }
        }
      });
    },
    // 划转
    transfer(index, currency) {
      let that = this;
      that.transferData.modalShow = true;
    },
    getAddress(name,addr){
      var that = this;
      that.address='';
      this.$http({
        url: this.API+"/walletMiddle/GetDrawAddress",
        method: "GET",
        params:{
          user_id:localStorage.getItem('user_id'),
          coin_name:name,
          contract_address:addr?addr:0
        }
      }).then(res => {
        if (res.data.code == 0) {
          that.address = res.data.data.address
        } else {
          that.address='';
          layer.msg(res.data.errorinfo)
        }
      });
    }
  },
  created() {
    this.token = localStorage.getItem("token") || "";
  },
  mounted() {
    var that = this;
    that.userInfo();
    that.getdata();
    this.init();
  }
};
</script>
<style scoped lang="scss">
.header {
  padding: 15px 30px;
  overflow: hidden;
}
.btn {
  width: 150px;
  height: 42px;
  line-height: 42px;
  border-radius: 3px;
  background: #5697f4;
  color: #fff;
  margin-left: 20px;
}
.min_lab {
  margin: 0 10px;
}
.min_lab input {
  margin-right: 3px;
}
.inp_lab {
  border: 1px solid #6b80ae;
  border-radius: 2px;
  padding: 3px 5px;
}
.inp_lab input {
  background: none;
  border: none;
  width: 120px;
  color: #fff;
}
.right_text {
  color: #5697f4;
}
.right_text span {
  cursor: pointer;
}
.record {
  margin-right: 10px;
}
.content_ul {
  padding: 0 20px;
}
.content_top {
  padding: 10px 20px;
  // background: #161617c7;
}
.content_li {
  padding: 15px 0;
  border-bottom: 1px solid #1e2c42;
}
.operation,
.copy,
.ewm {
  color: #5697f4;
}
.copy {
  margin: 0 30px;
}
.copy:hover {
  cursor: pointer;
}
.ewm:hover {
  cursor: pointer;
}
.operation span {
  cursor: pointer;
}
.hide_div {
  border: 1px solid #1e2c42;
  padding: 20px;
}
.excharge_record {
  color: #5697f4;
}
input {
  background: none;
  border: none;
}
.address_inp {
  width: 100%;
  border: 1px solid #6b80ae;
  border-radius: 3px;
  padding: 15px 15px;
}
.num_lab {
  display: flex;
  width: 100%;
  border: 1px solid #6b80ae;
  border-radius: 3px;
  padding: 15px;
}
.num_lab input {
  width: 100%;
}
.range_lab,
.get_lab {
  border: 1px solid #6b80ae;
  border-radius: 3px;
  padding: 15px;
}
.get_lab {
  background: #1e2c42;
}
.right_inp_wrap {
  margin-left: 20px;
}
.use_num,
.advance {
  color: #5697f4;
}
.use_num {
  margin-right: 5px;
}
.advance {
  margin-left: 5px;
}
.withdraw_btn {
  background-color: #7a98f7;
  color: #fff;
  padding: 15px 20px;
  min-width: 160px;
  border: none;
  border-radius: 5px;
}
.withdraw_btn:hover {
  cursor: pointer;
}
.bg {
  background: #2b3c71;
}
.ewm_wrap {
  position: relative;
}
.ewm_img {
  width: 120px;
  height: 120px;
  position: absolute;
  top: 25px;
  left: -30px;
  border: 10px solid #fff;
}
.hide {
  display: none;
}
.rec-box {
  .rec-con {
    margin: 10px;
    padding: 0 20px;
    background: #171c2b;
    span {
      flex: 1;
      text-align: center;
      line-height: 3;
    }
    .rec-title {
      display: flex;
      justify-content: space-between;
      font-size: 14px;
      color: #fff;
      line-height: 1.5;
    }
    li {
      display: flex;

      justify-content: space-between;
      font-size: 12px;
      color: #728daf;
      border-top: 1px solid #2e394c;
    }
  }
}
.more {
  width: 100%;
  line-height: 40px;
  text-align: center;
  cursor: pointer;
}
// 划转弹窗
.transfer-modal {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  overflow: hidden;
  justify-content: center;
  align-items: center;
}
.transfer-mask {
  width: 500px;
  background-color: #171c2b;
  margin: 0 auto;
  border-radius: 5px;
}
.transfer-header {
  line-height: 40px;
  text-align: center;
  position: relative;
}
.transfer-header p {
  position: absolute;
  right: 10px;
  top: 0;
}
</style>



