<template>
    <div>
        <div class="top">
            <p>{{$t('asset.deposit_record')}}({{deposit_type=='current'?$t('asset.deposit_current'):$t('asset.deposit_fixed')}})</p>
        </div>
        <div class="content_top flex alcenter fColor2 ft12" style="padding:0 30px">
            <p class="flex1 tc">{{$t('asset.currency')}}</p>
            <p class="flex1 tc">{{$t('asset.deposit_num')}}</p>
            <p class="flex1 tc">{{$t('asset.deposit_in')}}</p>
            <p class="flex1 tc">{{$t('asset.deposit_time')}}</p>
            <!-- <p v-if="deposit_type=='current'" class="flex1 tc">{{$t('asset.deposit_state')}}</p> -->
			<p v-if="deposit_type=='current'" class="flex1 tc">{{$t('td.do')}}</p>
        </div>
		<div v-if="list.length==0" class="noData">
			{{$t('asset.no_record')}}！
		</div>
        <ul class="list">
			<li v-for="(item,index) in list" :key="index" class="curPer">
			    <div class="balance_detail tc">
			        <div class="use_balance flex1">
			           <p class="use_balance_num ft12">{{item.currency}}</p>
			        </div>
			        <div class="convert flex1">
			           <p class="lock_balance_num ft12">{{item.num}}</p>
			        </div>
			        <div class="convert flex1">
			            <p class="lock_balance_num ft12">{{item.income}}</p>
			        </div>
			        <div class="flex1">
			            <p class="lock_balance_num ft12" >{{item.create_time}}</p>
			        </div>
					<div v-if="deposit_type=='current'&&item.status==1" class="convert flex1 ft12 operation">
					    <span @click="tradeOut(item.id)" style="color: 007AFF;">{{$t('asset.deposit_out')}}</span>
					</div>
					<div v-if="deposit_type=='current'&&item.status==0" class="convert flex1 ft12 operation">
					    <span  style="color: gray;">{{$t('asset.deposit_out')}}</span>
					</div>
					<div v-if="deposit_type=='current'&&item.status==2" class="convert flex1 ft12 operation">
					    <span  style="color: gray;">{{$t('asset.deposit_outed')}}</span>
					</div>
			    </div>
			</li>
        </ul>
    </div>
</template>
<script>
export default {
    data(){
        return{
		  deposit_type:'current',
          totle:'',
          list:[],
        //   usprice:''
        }
    },
    filters: {
    toFixeds: function(value) {
      value = Number(value);
      return value.toFixed(4);
    },
    toFixeds8: function(value) {
      value = Number(value);
      return value.toFixed(8);
    }
  },
    created(){
       this.token = localStorage.getItem('token') || '';
	   this.deposit_type=this.$route.query.deposit_type;
	   console.log(this.deposit_type)
    },
    mounted(){
       this.getDeposit();
    },
    methods:{
         //法币账户
         getDeposit(){
             var that = this
			this.$http({
				url: '/api/' + 'fixed/get_deposit_lists',
				method:'post',
				data:{
					deposit_type:that.deposit_type=='fixed'?1:2
				},
				headers: {'Authorization':  that.token}
			}).then(res=>{
				if(res.data.type  =='ok'){
					console.log(res)
					that.list = res.data.message;
				}else{
					// layer.msg(res.message);
				}
			}).catch(error=>{
				console.log(error)
			})
        },
		tradeOut(id){
			console.log(id)
			var that = this
			this.$http({
				url: '/api/' + 'fixed/ex_current_deposit_record',
				method:'post',
				data:{
					r_id:id
				},
				headers: {'Authorization':  that.token}
			}).then(res=>{
				if(res.data.type  =='ok'){
					layer.msg(res.data.message);
				}else{
					layer.msg(res.data.message);
				}
			}).catch(error=>{
				console.log(error)
			})
		}
    }
}
</script>
<style scoped>
	.noData{
		font-size: 16px;
		margin: 30px;
		width: 100%;
		color: gray;
		text-align: center;
	}
    .operation{
        color: #5697f4;
    }
    .flex1{
        flex: 1;
    }
   .top{
       /* background: #1b1e2e; */
       color: #fff;
       padding: 15px 30px;
   }
   .all_account{
       color: #61688a;
   }
   .list{
       padding: 15px 30px;
   }
   .balance_detail{
       display: flex;
       justify-content: space-between;
       align-items: center;
       color: #61688a;
   }
   .legal_name{
       color: #61688a;
       padding: 10px 0;
   }
   .use_balance_num,.lock_balance_num,.lock_balance_num{
       color: #fff;
       padding: 10px 0;
       margin-top: 5px;
   }
   .list li{
       border-bottom: 1px solid #1B2A3E;
   }
   .convert{
       padding: 10px 0;
   }
</style>



