      active:0,
      value1:false,
      collect:0,
      current:0,
      marketList:[],
      myAdd:[],
      token:''
    };
  },
  created() {
    // this.init(this.initKline);
    this.token = window.localStorage.getItem('token') || '';
    if(this.token){
      this.getMyAdd();
    }else{
     this.getQuotation();
    }
  },
  destroyed () {
    // window.removeEventListener('scroll', this.handleScroll)
  },
  mounted() {
    // window.addEventListener('scroll', this.handleScroll)
    this.getSwiper();
    this.connect();
    this.getNews();
    this.aboutUs();
    this.help();
    // this.getKile();
  },
  methods: {
    //行情曲线绘制
    draws(k,arr_price,color){
      var drawLine = {};
        !function () {
            //求数组最大值
            Array.prototype.max = function () {
                var max = this[0];
                var len = this.length;
                for (var i = 1; i < len; i++) {
                    if (this[i] > max) {
                        max = this[i];
                    }
                }
                return max;
            }
            function draw(arr) {
                var width = 100, height = 50;
                var maxV = arr.max();
                //计算y轴增量
                var yStep = height / maxV;
                // console.log(maxV);
                var domCan = document.getElementsByClassName("canDom")[k];
                var g = domCan.getContext("2d");
                //g.rotate(180 * Math.PI / 180);//旋转90度
                //g.translate(50, 0);
                //g.scale(1,-1);
                g.beginPath();
                g.lineWidth = 2;
                g.strokeStyle = color;
                //g.moveTo(0, 0);//开始画图的位置
                var x_space = width / (arr.length - 1);//水平点的间隙像素
                //g.lineTo(15, 60);  x,y,x表示水平向右延伸，y表示垂直高度位置，从画板最左上角(0,0)开始计算坐标
                var xLen = 0;
                for (var i = 0; i < arr.length; i++) {
                    var yValue = arr[i];//纵坐标值
                    xLen += x_space;
                    var yPont = height - yValue * yStep;
                    if (i == 0) {
                        xLen = 0;
                    }
                    var m = xLen + "," + yPont;
                    // console.log(m);
                    g.lineTo(xLen, yPont);
                }
                //g.lineTo(15, 60);
                //g.lineTo(20, 30);
                //g.lineTo(30, 40);
                //g.lineTo(40, 50);
                g.stroke();
                g.closePath();
            }
            drawLine.minCurve = draw;
            //window.drawMinLine = draw;
        }();
            // var arr = [10, 50, 30, 70, 80, 40, 90, 110, 80, 60, 69, 80, 80, 10, 50, 30, 70, 80, 40, 97, 80, 70, 60, 50, 80];
            //var arr = [30, 80,50,70];
            //drawMinLine(arr);
            drawLine.minCurve(arr_price);
    },
    // handleScroll () {
    //   var scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
    //   var top = $('.feature_list').offset().top

    //   console.log(top+'/'+scrollTop)
    // },
    register(){
      this.$router.push({name:'register',query: { haccount: this.haccount}})
    },
    //去交易
    goTrade(l_id,c_id,c_name,l_name){
      // window.localStorage.setItem("legal_id", l_id);
      // window.localStorage.setItem("currency_id", c_id);
      // window.localStorage.setItem("legal_name", l_name);
      // window.localStorage.setItem("currency_name", c_name);
      // var symbol = c_name + "/" + l_name;
      // window.localStorage.setItem("symbol", symbol);
      this.$router.push('/dealCenter')
    },
    getSwiper() {
      this.$http({
        url: "/api/news/list",
        method: "post",
        data:{
          c_id:5
        }
      })
        .then(res => {
          // console.log(res);
          if (res.data.type == "ok") {
            this.swiperImgs = res.data.message.list;
          }
        })
        .then(() => {
          var mySwiper02 = new Swiper(".banner_wrap", {
            // direction: 'vertical',
            loop: true,
            autoplay: 3000,
            speed:1000,
            // 如果需要分页器
            pagination: ".swiper-pagination02",
            paginationClickable: true,
            autoplayDisableOnInteraction: false,
            observer: true, //修改swiper自己或子元素时，自动初始化swiper
            observeParents: true //修改swiper的父元素时，自动初始化swiper
          });
        });
    },
    connect() {
      var that = this;
      that.$socket.emit("login", localStorage.getItem("user_id"));
      that.$socket.on("daymarket", msg => {
        if (msg.type == 'daymarket') {
          var cname = msg.symbol;
          var change = msg.change;
          var now_price = msg.now_price;
          var volume = msg.volume;
          $("div[data-name='" + cname + "']")
            .find(".now_price")
            .html((now_price -0).toFixed(4));
          $("div[data-name='" + cname + "']")
            .find(".volume")
            .html((volume-0).toFixed(4));
          $("div[data-name='" + cname + "']")
            .find(".now_cny")
            .html((now_price *6.9641).toFixed(4)+' CNY');

          $("div[data-name='" + cname + "']")
            .find("button")
            .text(change+'%');
          if(change<0){
             $("li[data-name='" + cname + "']")
            .find("button")
            .addClass('downcolor');
          }else{
            $("li[data-name='" + cname + "']")
            .find("button")
            .removeClass('downcolor')
          }
          $.each(this.marketList,function(k,v){
            $.each(v,function(i,j){
             if(j.legal_id == msg.legal_id && j.currency_id == msg.currency_id){
               j.change = msg.change;
               j.now_price = msg.now_price;
               j.high = msg.high;
               j.low = msg.low;
             }
          })
          })
        }
      });
    },
    setPercent(a, b) {
      if (a - b == 0) {
        return "0%";
      } else if (a == 0) {
        return "-100%";
      } else if (b == 0) {
        return "+100%";
      } else {
        var p = ((a - b) / b / 100).toFixed(2);
        if (p > 0) {
          p = "+" + p + "%";
        } else {
          p = p + "%";
        }
        return p;
      }
