          this.getMyAdd();
        })
      } else {
        layer.msg('请先登录')
      }
    },
    getQuotation() {
      var that = this;
      this.$http({
        url: "/api/currency/quotation_new",
        method: "get"
      }).then(res => {
        if (res.data.type == "ok" && res.data.message.length != 0) {
          // this.nowCoin = this.quotation[0].name;
          var msg = res.data.message;
          this.quo_list = msg;
          this.quotation=msg.find((item) => item.name == 'USDT').quotation;
          msg.map(function(item,index){//判断当前OTC
            if(item.name == 'USDT'){
              return that.current = index;
            }
          })
          if(this.myAdd.length){
            // alert(1)
            msg.forEach((item,index) => {
              this.myAdd.forEach((ite,ind) => {
                if(item.id == ite.legal_id){
                  item.quotation.find((c) => {
                    return c.currency_id == ite.currency_id;
                  }).added = true
                }
              })
            })
          }
          console.log(msg)
          var arr = [];
          for(var i=0;i<msg.length;i++){
            arr[i] = msg[i].quotation
          };
          that.marketList = arr;
          console.log(that.marketList)
          //绘图
          var color;
          // var arr = [10, 50, 30, 70, 80, 40, 90, 110, 80, 60, 69, 80, 80, 10, 50, 30, 70, 80, 40, 97, 80, 70, 60, 50, 80];
          that.$http({
            url: "/api/currency/show_new_timeshar?period=1day",
            method: "get"
          }).then(res => {
              var krr = res.data.data;
              // console.log(krr)
              setTimeout(()=>{
                $.each(this.quotation,function(k,v){
                  if(parseFloat(v.change)<0){
                    color = '#ef4034';
                  }else{
                    color = '#0d8551';
                  }
                  that.draws(k,krr[k],color)
                })
              },1000)
          });
        }
      });
    },
