  align-items: center;
  justify-content: space-around;
}
.withdraw {
  margin: 0 4px;
}
.carousel .last a:hover {
  cursor: pointer;
}
.sliders {
  width: 100%;
  max-height: 540px;
  overflow: hidden;
  img{width: 100%;display: block;}
  @media screen and (max-width:1560px){
    // height: 465px;
    // img{
    //   width: 100%;
    //   height: 465px;
    // }
  }
}
@media screen and (max-width:1410px){
  // .mt{margin-top:5px;}
}
.swiper-container-horizontal>.swiper-pagination-bullets{
  bottom: 25px;
}
.swiper-pagination-bullet{
  // width: 56px!important;
  height: 3px;
  border-radius: 0;
  background: #fff;
}

.notice_ul {
  margin: 0 40px;
  padding: 5px 0;
  // background: #171c2b;
  overflow: hidden;
  word-break: keep-all;
}
.notice_ul > li {
  // flex: 1;
  font-size: 12px;
  padding: 0 15px;
  text-align: center;
  color: #9eb5ca;
  word-break: keep-all;
  position: relative;
}
.notice_ul > li::after {
  content: "/";
  position: absolute;
  left: -0.5em;
  color: #9eb5ca;
}
.notice_ul > li:last-child:after {
  content: "";
  color: #6b80ae;
}
.notice_ul > li:hover {
  color: #6b80ae;
  cursor: pointer;
}
.feature_wrap {
  background-color: #fff;
  padding: 90px 0;
  text-align: center;
  color: #54748f;
  line-height: 2em;
}
.feature_wrap h2 {
  font-size: 28px;
  margin-bottom: 10px;
  color: #192544;
}
.feature_wrap .feature_list {
  // padding-top: 80px;
  width: 1200px;
  margin: 0 auto;
}
.feature_wrap .feature_list li {
  float: left;
  width: 22%;
  background: transparent none no-repeat top;
  // padding-top: 140px;
  // padding-top: 90px;
  padding: 70px 0;
  border-radius: 10px;
  >h3{
    margin-left: -28px;
  }
  >p{
    font-size: 14px;
    line-height: 1.5em;
  }
  &:hover{
    box-shadow: 0 0 25px #e0e0e0;
    img{
      animation: myfirst .8s;
    }
  }
  @keyframes myfirst {
      0% {
          transform: translate(0);
      }
      50% {
          transform: translateY(-20px);
      }
      60% {
          transform: translate(0);
      }
      70% {
          transform: translateY(-10px);
      }
      80% {
          transform: translate(0);
      }
      90% {
          transform: translateY(-5px);
      }
      100% {
          transform: translate(0);
      }
  }
}
