.feature_wrap h3 {
  font-size: 18px;
  font-weight: 700;
  margin-bottom: 10px;
  color: #192544;
}
.notice_box{
    width: 100%;
    height: 52px;
    // border-top: 1px solid rgba(29,50,66,.7);
    margin-top: -1px;
    background-color: #202030;
    position: relative;
    >div{
      margin: 0 0px;
      font-size: 12px;
      line-height: 52px;
      // height: 36px;
      overflow: hidden;
      word-break: keep-all;
      text-align: center;
      span{
        // display: inline-block;
        position: relative;
        padding: 0 0em 0 0.5em;
        color: #e8e8e8;
        cursor: pointer;
      }
      // span:before {
      //   content: "·";
      //   position: absolute;
      //   left: -.5em;
      //   color: #54748f;
      //   font-size: 30px;
      // }
      // span:nth-child(1):before{
      //   content: "";
      // }
      span:hover{
        color: #357ce1;
      }
    }
}
.client_wrap{
  width: 100%;
  // height: 710px;
  color: #9eb5ca;
  text-align: center;
  line-height: 2em;
  background-color: #fff;
  .wrap_in{
    // width: 1300px;
    margin: 0 auto;
    height:430px;
    padding-top: 100px;
    padding-right: 50px;
    // background-color: #152940;
    background: url(../assets/images/home_bg.png) no-repeat;
    background-size: cover;

    h2{
      margin-bottom: 24px;
      color: #fff;
      font-weight: 400;
      font-size: 28px;
    }
    .nextp{color:#9eb5ca}
    ul{
      width: 100%;
      margin: 0 auto;
      text-align: left;
      justify-content: flex-end;
      li{
        background: #100b26;
        margin: 0 15px;
        color: #fff;
        font-size: 14px;
        padding: 0 15px;
        // padding: 40px 0;
        line-height: 40px;
        position: relative;
        cursor: pointer;
        p{
          height: 40px;
          width: 40px;
          margin: 0 10px 0 0;
          vertical-align: top;
          border-radius: 50%;
          background:  none no-repeat 50%;
          background-image: url(../assets/images/p0.svg)
        }
        .p1{
          background-image: url(../assets/images/p1.svg)
        }
        .p2{
          background-image: url(../assets/images/p2.svg)
        }

        .codebox{
          position: absolute;
          left: 50%;
          top: 70px;
          -webkit-transform: translate(-50%);
          -ms-transform: translate(-50%);
          transform: translate(-50%);
          -webkit-box-sizing: border-box;
          box-sizing: border-box;
          width: 120px;
          height: 130px;
          border: 1px solid #357ce1;
          border-radius: 2px;
          background: #fff none no-repeat 12px 12px/96px 96px;
          text-align: center;
          color: #357ce1;
          font-size: 12px;
          line-height: 18px;
          z-index: 6;
          display: none;
          img{
            width: 100px;
            height: 100px;
            margin: 0 auto;
            display:block;
            margin-top: 10px;
          }
        }
      }
      li:hover .codebox{
        display: block;
      }
    }
    .mon_mind{
      width: 518px;
      position: relative;
      margin: 0 auto;
      >div{
        width: 100%;
        .pcimg{
          width: 518px;
          margin: 0 auto;
        }
      }
      .mobimg{
        width: 122px;
        position: absolute;
        right: -60px;
        bottom: -20px;
        width: 122px;
        height: 253px;
        background: #0a151e none no-repeat 50%/cover;
        -webkit-box-shadow: -9px 12px 62px 0 rgba(36,52,69,.3);
        box-shadow: -9px 12px 62px 0 rgba(36,52,69,.3);
        border-radius: 16.47px;
      }
    }
  }
}
.register_wrap{
  height: 240px;
  width: 100%;
  // border-top: 1px solid #cad7e0;
  background: #fff;
  h2{
    color: #232a4a;
    font-size: 22px;
    line-height: 24px;
  }
  .rebox{
    margin: 32px auto 0 auto;
  }
  .input-register{
    width: 400px;
    height: 40px;
    font-size: 12px;
    color: #000;
    border: 1px solid #bdced9;
    border-radius: 2px;
    padding-left: 16px;
  }
  button{
    margin-left: 8px;
    display: inline-block;
    height: 40px;
    padding: 0 44px;
    font-size: 16px;
    min-width: 120px;
    -webkit-appearance: none;
    background-color: #357ce1;
    color: #fff;
    border: none;
    outline: none;
    border-radius: 2px;
    cursor: pointer;
  }
  button:hover{
    opacity: .8;
  }
}
}
.coins_wrap{
  padding-top: 60px;
}
.coins_wrap>div{
  width: 1200px;
  margin: 0px auto;
}
.legal_coin p{
    width: 142px;
    padding: 10px 0;
    text-align: center;
    background-color: #fff;
    color: #a8a8a8;
    margin-right: 14px;
    -o-box-shadow: 0 0 5px 0 rgba(32,32,48,.26);
    box-shadow: 0 0 5px 0 rgba(32,32,48,.26);
    border-radius: 5px;
    justify-content: center;
    cursor: pointer;
}
.legal_active{
    box-shadow: 0 0 5px 0 #363636!important;
    // color: #363636!important;
}
.coin_title{
  font-size: 12px;
  color: #6a6a6a;
  background: #e8ebf6;
  padding: 15px 20px;
}
.coin_title span{
  width: 10%;
}
.currencys{
  padding: 10px 20px;
}
.currencys>p{
  width: 10%;
}
.ac_pow{
  color: #0d8551;
}
.label-span em{
  padding: 2px 0px;
  width: 35px;
  border-radius: 2px;
  color: #fff;
  text-align: center;
  display: block;
}
.label-span{
  .pow{
    background: linear-gradient(to right,#9e8cfb,#5c68f3);
  }
  .pot{
    background: linear-gradient(to right,#47f3db,#24c48e);
  }
  .pos{
    background: #17cef0;
  }
  .ifo{
    border: 1px solid #17cff0;
    color: #17cff0
  }
  .ptc{
    background: linear-gradient(to right,#5ee763, #1fb925);
  }
  .poc{
    background: linear-gradient(to right,#e97956, #cc3607);
  }
  .other{
    background: linear-gradient(#9e8cfb,#5c68f3);
  }
}

.change{
  padding: 7px 0;
  border-radius: 3px;
  color: #0d8551;
}
.change1{
  background: #d4f6f0;
}
.downcolor{
  color: #ef4034;
  background: #ffe4e4;
}
.ul-ul li div:hover{
  background: #f0f0ff;
}
.pr10{
  padding-right:10px;
}
</style>



